
export default class moltin {

	/*
	  The below annotation will be processes by ngAnnotate, which
	  will annotate the constructor after compiling for minification.
	  */
	  /*@ngInject;*/
	  constructor($q) {
	  	this._$q = $q;
	  }

	  MoltinAuth($q) {
	  	var deferred = $q.defer();
	  	var moltin = new Moltin({publicId: 'Qwml0w0dVlYgV9EIf0xH0dVjiWpct2Ix3ZYyS7nthA'});
	  	moltin.Authenticate(function() {
	  		deferred.resolve(moltin);
	  	});
	  	return deferred.promise;
	  }
	}