/*
 Import all Angular components via ES6 imports and register them
 at your module via their corresponding functions (controller, service, etc.).
 */

import MainCtrl from './controllers/HomeController';
import angRoute from 'angular-route';

angular.module('myApp', [
    angRoute
])
    .controller('MainCtrl', MainCtrl)
    .config(function config($routeProvider, $locationProvider) {
        $routeProvider
            .when('/', {
                controller: 'MainCtrl',
                template: `
					<div class="main-home">
					    <div class="main-home__content">
                            <div class="home-content_left">
                                <i class="material-icons">add_to_queue</i>
                                <i class="material-icons">alarm</i>
                                <i class="material-icons">business</i>
                            </div>
                            <div class="home-content_right">
                                <p class="home-content_right_desc">Более 5 законченных больших проектов</p>
                                <p class="home-content_right_desc">Более 3 лет личного опыта</p>
                                <p class="home-content_right_desc">Более 2 компаний пользуются по нынешний день нашими услугами</p>
                            </div>
                        </div>
                    </div>
					
					`
            })
            .when('/portfolio', {
                controller: 'MainCtrl',
                template: `
                    <div class="main-portfolio">
                        <div class="main-portfolio__content">
                           <div class="main-portfolio__item">
                                <h1 class="main-portfolio__title">Arcona</h1>
                                <a href="http://arconapro.ru/" target="_blank" class="port__link">Перейти</a>
                            </div>
                            <div class="main-portfolio__item">
                                <h1 class="main-portfolio__title">ИНКРО</h1>
                                <a href="http://devinkro.esy.es/" target="_blank" class="port__link">Перейти</a>
                            </div>
                            <div class="main-portfolio__item">
                                <h1 class="main-portfolio__title">Express</h1>
                                <a href="http://131.153.5.170:8800/?p=templates-homepage" target="_blank" class="port__link">Перейти</a>
                            </div>
                        </div>
                    </div>
                    `
            })
            .when('/skills', {
                controller: 'MainCtrl',
                template: `
                    <div class="main-skills">
                        <div class="main-skills__content">
                            <div class="main-skills_item">
                                <i class="fa fa-television" aria-hidden="true"></i>
                                <h1 class="skills__title">WWW</h1>
                                <p class="skills__desc">Мы используем только передовые технологии</p>
                            </div>
                            <div class="main-skills_item">
                                <i class="fa fa-mobile" aria-hidden="true"></i>
                                <h1 class="skills__title">MOBILE</h1>
                                <p class="skills__desc">Android/ios</p>
                            </div>
                        </div>
                    </div>
                `
            });
        $locationProvider.html5Mode(true);
    });

